<template>
  <ul class="c-short-statistics">
    <li>Soma: {{ sum.toFixed(2) }}</li>
    <li>Média: {{ mean.toFixed(2) }}</li>
    <li>Desvio padrão: {{ sd.toFixed(3) }}</li>
    <li>Variação: {{ variance.toFixed(3) }}</li>
  </ul>
</template>
<script>
export default {
  name: 'ShortStatistics',
  props: {
    total: {
      type: Number,
      default: 0
    },
    set: {
      type: Array,
      default: () => []
    }
  },
  computed: {
    items() {
      return this.set.map((item) => parseFloat(item))
    },
    sum() {
      return this.items.reduce((acc, item) => (acc += item), 0)
    },
    divisor() {
      return this.items?.length || 1
    },
    mean() {
      return this.sum / this.divisor
    },
    sd() {
      return Math.sqrt(this.variance)
    },
    variance() {
      return (
        this.items
          .map((item) => {
            return Math.pow(item - this.mean, 2)
          })
          .reduce((acc, item) => (acc += item), 0) / (this.divisor - 1 || 1)
      )
    }
  }
}
</script>
<style src="./style.styl" lang="stylus" scoped />
