<template>
  <div class="v--flex-gap v--flex-align-right">
    <Button
      class="v--bg-grey"
      @click="$emit('share-survey')"
      :loading="$wait.is('survey')"
    >
      <!-- <div class="v--flex"> -->
      <Icon name="share" />
      <Spacing left="1" class="v--hide-xs">Compartilhar </Spacing>
      <!-- </div> -->
    </Button>
    <Button
      class="v--bg-grey"
      @click="$emit('delete-survey')"
      :loading="$wait.is('survey')"
    >
      <!-- <div class="v--flex"> -->
      <Icon name="trash" />
      <Spacing left="1" class="v--hide-xs">Excluir </Spacing>
      <!-- </div> -->
    </Button>
  </div>
</template>
<script>
import { mapState } from 'vuex'
export default {
  name: 'SurveyToolbar',
  emits: ['delete-survey'],
  computed: {
    ...mapState({
      repo: (state) => state.repos.repo
    }),
    shareLink() {
      return `${window.location.href}&repo=${this.repo.id}&shareLink`
    },
    githubLink() {
      return `https://github.com/${this.$route.params.org}/${this.$route.params.repo}`
    }
  },
  methods: {
    copyShareLink() {
      this.$copyText(this.shareLink)
        .then(() => this.$toast.success('Link copiado!'))
        .catch(() => this.$toast.error('Falha ao copiar'))
    }
  }
}
</script>
