<template>
  <div class="p-file__container">
    <div class="p-file__title-bar">
      <Spacing right="1">
        <GoBackButton :to="goBackRoute" />
      </Spacing>
      <div style="width: 100%">
        <div class="v--flex">
          <div>
            <h1 class="p-file__title">Alterações privadas</h1>
            <small>
              Tudo o que for feito nesta tela será criptografado e, apenas você
              poderá ver. A menos que você publique suas modificações.
            </small>
            <div>
              <small>
                Além disso, aqui você não pode alterar a classificação da
                questão, nem a classificação das respostas.
              </small>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div>
      <QuestionComponent
        v-if="question"
        :question="question"
        :encrypt="true"
        kind="codes"
        @show-answers="showAnswers"
        @show-latex="showLatexModal"
        @do-comment="showingQuestion = question"
        @remove-comment="removingComment = $event"
        @add-tag="addTag"
        @remove-tag="removeTag"
        @show-charts="showChartsModal = $event"
      />
    </div>
    <CommentModal
      v-if="showingQuestion"
      :loading="$wait.is('commenting-question')"
      @close="showingQuestion = null"
      @comment="comment"
    />
    <CommentDeleteModal
      v-if="removingComment"
      :comment="removingComment.comment"
      @delete="deleteQuestionComment"
      @close="removingComment = null"
    />
    <Modal v-if="latex" title="Código LaTeX" @close="latex = null">
      <LatexChart :total="latex.total" :set="latex.set" />
    </Modal>
    <SubmissionModal
      v-if="showingAnswers"
      :questions="questions"
      :showing-answers="showingAnswers"
      read-only
      @archive="archive"
      @close="showingAnswers = []"
    />
    <ChartsModal
      v-if="showChartsModal"
      :question="showChartsModal"
      @close="showChartsModal = null"
    />
  </div>
</template>
<script>
import { mapState } from 'vuex'
import QuestionComponent from '@/components/pesquisa/Question/Question.vue'
import { QuestionClassifier } from '@/plugins/question-classifier'
import textColor from '@/mixins/text-color'
import CommentModal from '@/components/pesquisa/CommentModal/CommentModal'
import CommentDeleteModal from '@/components/pesquisa/Comment/DeleteModal/DeleteModal.vue'
import ChartsModal from '@/components/pesquisa/Question/Charts/Charts.vue'
import survey from '@/mixins/survey'
import SubmissionModal from '@/components/pesquisa/Submissions/SubmissionModal/SubmissionModal.vue'

export default {
  name: 'QuestionPage',
  route: '/pesquisas/:org/:repo/:file/:question',
  layout: 'painel',
  components: {
    SubmissionModal,
    QuestionComponent,
    CommentModal,
    ChartsModal,
    CommentDeleteModal
  },
  mixins: [survey, textColor],
  data() {
    return {
      coding: true,
      showingQuestion: null,
      showChartsModal: null,
      showingAnswers: [],
      removingComment: null,
      latex: null
    }
  },
  computed: {
    ...mapState({
      privated: (state) => state.codes.codes,
      survey: (state) => state.surveys.survey,
      questions: (state) => state.questions.questions,
      answers: (state) => state.answers.answers,
      noanswers: (state) => state.answers.noanswers,
      filtereds: (state) => state.answers.filtereds,
      user: (state) => state.auth.user
    }),
    question() {
      return (
        this.questions.length &&
        this.questions[parseInt(this.$route.params.question - 1)]
      )
    },
    githubLink() {
      return `https://github.com/${this.$route.params.org}/${this.$route.params.repo}`
    },
    goBackRoute() {
      return `/pesquisas/${this.$route.params.org}/${this.$route.params.repo}/${this.$route.params.file}??aba=resultados`
    }
  },
  created() {
    this.codes(this.user)
      .get()
      .catch((e) => {
        if (e?.response?.status === 404) {
          this.codes(this.user)
            .create({})
            .then(() => {
              this.codes(this.user).get()
            })
        }
      })

    this.surveys()
      .get()
      .then(({ survey }) => {
        return this.updateSurveyData(survey)
      })
      .finally(() => {
        this.$wait.end('survey')
      })
    this.template = localStorage.getItem('questions-template')
  },
  methods: {
    showAnswers(answers) {
      this.showingAnswers = answers
      this.currentLine = 0
    },
    showLatexModal(e) {
      this.latex = e
    },
    removeTag({ tags, tag }) {
      this.codes(this.user)
        .update({ tags }, `Remove tag`)
        .then(() => {
          this.codes(this.user).get()
          this.$toast.success(`Tag "${tag.name}" removida`)
        })
        .finally(() => this.$wait.end('changing-tag'))
    },
    addTag({ tags, tag }) {
      this.codes(this.user)
        .update({ tags }, `Add tag`)
        .then(() => {
          this.codes(this.user).get()
          this.$toast.success(`Tag "${tag.name}" adicionada`)
        })
        .finally(() => this.$wait.end('changing-tag'))
    },
    comment(message) {
      this.$wait.start('commenting-question')
      const comment = {
        message,
        author: `${this.user.login}@${this.user.id}`
      }

      this.comments()
        .onQuestion(comment, this.showingQuestion.column, 'codes', this.user)
        .then(() => {
          return this.codes(this.user)
            .get()
            .then(() => this.$toast.success('Comentário registrado!'))
        })
        .finally(() => {
          this.showingQuestion = null
          this.$wait.end('commenting-question')
        })
    },
    deleteQuestionComment() {
      this.$wait.start('removing-comment')
      this.comments()
        .deleteFromQuestion(
          this.removingComment.comment,
          this.removingComment.question.column,
          'codes',
          this.user
        )
        .then(() => {
          return this.codes(this.user)
            .get()
            .then(() => this.$toast.success(`Comentário removido`))
        })
        .finally(() => {
          this.removingComment = null
          this.$wait.end('removing-comment')
        })
    },
    createCodingTag(tag) {
      this.createTag(this.user, tag)
    },
    ativarCoding() {
      this.getUserCoding(this.user).then(() => {
        this.coding = true
      })
    },
    updateSurveyData(survey) {
      const classifier = new QuestionClassifier()
      const filtereds = survey.filtereds || []
      const questions =
        survey?.classifications?.map((classification, index) => {
          const answers = survey.answers
            .map((answer, line) => ({
              text: answer[index],
              line
            }))
            .map((answer) => {
              const sentiment = this.$answerClassifier.sentiment(
                answer.text,
                this.survey.language
              )

              let text = answer.text

              if (
                this.$questionClassifier.getOption(classification).answers
                  .printStyle === 'OpenQuestion'
              ) {
                const negativeWords = [...new Set(sentiment.negative)].sort(
                  (a, b) => a.length - b.length
                )
                const positiveWords = [...new Set(sentiment.positive)].sort(
                  (a, b) => a.length - b.length
                )

                negativeWords.forEach((word) => {
                  const regex = new RegExp(
                    '(?:[^.\\w]|^|^\\W+)' +
                      word +
                      '(?:[^.\\w]|\\W(?=\\W+|$)|$)',
                    'gi'
                  )
                  text = text.replace(
                    regex,
                    ` <span class="v--text-tag v--bg-negative">${word}</span> `
                  )
                })

                positiveWords.forEach((word) => {
                  const regex = new RegExp(
                    '(?:[^.\\w]|^|^\\W+)' +
                      word +
                      '(?:[^.\\w]|\\W(?=\\W+|$)|$)',
                    'g'
                  )
                  text = text.replace(
                    regex,
                    ` <span class="v--text-tag v--bg-positive">${word}</span> `
                  )
                })
              }

              return {
                ...answer,
                sentiment,
                text,
                originalText: answer.text
              }
            })
          return {
            title: survey.questions[index],
            classification,
            type: classifier.getType(classification),
            column: index,
            answers: answers.filter((i) => !filtereds.includes(i.line)),
            noanswers: answers.filter((i) => filtereds.includes(i.line))
          }
        }) || []
      const answers = survey.answers.filter(
        (i, index) => !filtereds.includes(index)
      )
      const noanswers = survey.answers.filter((i, index) =>
        filtereds.includes(index)
      )
      this.currentAnswerIndex = 0
      this.$store.commit('answers/setFiltereds', filtereds)
      this.$store.commit('answers/setAnswers', answers)
      this.$store.commit('answers/setNoAnswers', noanswers)
      this.$store.commit('questions/setQuestions', questions)
      return this.$store.commit('surveys/setSurvey', survey)
    }
  }
}
</script>
