<template>
  <div class="p-site">
    <Navbar v-if="user?.name" class="p-site__navbar" />
    <SiteNavbar v-else @click-login="login" class="p-site__navbar" />
    <Loader v-if="$wait.is('login')" />
    <section
      class="p-site__banner"
      style="
        color: #fff;
        background: #1d9bf0 url('https://gaintime.github.io/assets/img/bg.jpg');
      "
    >
      <h1>Central de Ajuda</h1>
    </section>
    <section class="p-site__section">
      <ToggleCard
        v-for="help in helps"
        :key="help.title"
        :title="help.title"
        :description="help.description"
      />
    </section>
    <footer class="p-site__footer">
      <p>
        Lyzeli, {{ year }}. Todo o conteúdo está sob a licença
        <a
          href="https://opensource.org/licenses/mit-license.php"
          target="_blank"
          >MIT</a
        >.
      </p>
      <p>
        Acompanhe Lyzeli no
        <a href="https://github.com/arieslab/lyzeli" target="_blank">GitHub</a>
      </p>
    </footer>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import Navbar from '@/components/layouts/Navbar/Navbar.vue'
import SiteNavbar from '@/components/layouts/Navbar/SiteNavbar.vue'

export default {
  name: 'help',
  route: '/ajuda',
  components: {
    Navbar,
    SiteNavbar
  },
  computed: {
    helps() {
      return [
        {
          title: 'Como adicionar uma pesquisa?',
          description: `<p>
        Para adicionar uma nova pesquisa você deve clicar no botão "Criar nova
        pesquisa".
      </p>
      <p>
        A partir daí, você escolhe se quer fazer o upload do arquivo da pesquisa
        primeiro, ou se quer apenas descrever a sua pesquisa, adicionando
        informações primeiro.
      </p>
      <br />
      <p>
        <b>Detalhe:</b> O arquivo da pesquisa deve ser um TSV ou XLSX, e a primeira
        linha deve conter as perguntas, e as demais linha devem conter as
        respostas.
      </p>`
        },
        {
          title: 'Quais formatos de arquivo são aceitos?',
          description: `<p>Os formatos aceitos são:</p><ul><li><b>TSV:</b> Tab-separated values</li><li><b>XLSX</b>: Formato de arquivo do Excel, facilmente encontrado em outros softwares de planilha.</li></ul> `
        },
        {
          title: 'Posso confiar os dados da minha pesquisa à Lyzeli?',
          description: `<p>Somos suspeitos para falar, mas <b>SIM</b>!</p>
          <br/>
          <p>Lyzeli não possui um sistema de banco de dados, então nenhum desenvolvedor de Lyzeli terá acesso ao que você inserir na plataforma, porque nós não temos esses dados, eles são seus.</p>
          <br/>
          <p>Todo o conteúdo que você adicionar, será salvo no repositório do <a href="https://github.com" target="_blank">GitHub</a> que você informar, então se você concorda com as <a href="https://docs.github.com/en/site-policy/privacy-policies/github-privacy-statement" target="_blank">políticas de privacidade do GitHub</a> e confia nelas, não precisa se preocupar.</p>
          <br/>
          <p>Lembre-se de sempre utilizar repositórios privados!</p>


          `
        },
        {
          title:
            'Por quê Lyzeli pede acesso a tantas funções e repositórios do GitHub?',
          description: `<p>Infelizmente o serviço do GitHub não nos dá a opção de pedir acesso apenas ao que vamos utilizar.</p>
          <br/>
          <p>Caso você não esteja seguro em compartilhar mais do que o repositório da pesquisa, aconselhamos que você crie uma conta do GitHub exclusiva para conteúdos que deseja explorar com Lyzeli.</p>
          <br/>
          <p>Mas asseguramos que não há riscos em compartilhar os dados. Lyzeli não acessa nada além do que você visualiza na interface de Lyzeli.</p>
          <p>Caso você queira, pode ainda explorar todo o código fonte de Lyzeli para averiguar as operações que são feitas.</p>
          `
        }
      ]
    },
    ...mapState({
      user: (state) => state.auth.user
    }),
    year() {
      return new Date().getFullYear()
    },
    set() {
      return this.groups.reduce((set, item) => {
        const key = item.text === '' ? '' : item.text
        set = {
          ...set,
          [key]: set[key] ? set[key] + 1 : 1
        }

        return set
      }, {})
    },
    words() {
      return this.$word.count(
        'ontrary to popular belief, Lorem Ipsum is not simply random text. It has roots in a piece of classical Latin literature from 45 BC, making it over 2000 years old. Richard McClintock, a Latin professor at Hampden-Sydney College in Virginia, looked up one of the more obscure Latin words, consectetur, from a Lorem Ipsum passage, and going through the cites of the word in classical literature, discovered the undoubtable source. Lorem Ipsum comes from sections 1.10.32 and 1.10.33 of "de Finibus Bonorum et Malorum" (The Extremes of Good and Evil) by Cicero, written in 45 BC. This book is a treatise on the theory of ethics, very popular during the Renaissance. The first line of Lorem Ipsum, "Lorem ipsum dolor sit amet..", comes from a line in section 1.10.32.',
        'ptBr'
      )
    }
  },
  methods: {
    login() {
      this.$wait.start('login')
      this.$github
        .login()
        .then(() => this.$router.push('/inicio'))
        .finally(() => this.$wait.end('login'))
    }
  }
}
</script>
