<template>
  <Modal @close="$emit('close', $event)" title="Compartilhar pesquisa" small>
    <div style="display: grid; grid-gap: 8px" v-if="!link.length">
      <Field
        name="collaborator"
        label="Informe o nome de usuário da pessoa para gerar um link de compartilhamento"
        placeholder="@..."
        :disabled="$wait.is('getting-link')"
        v-model="collaborator"
      />
      <small>
        OBS: Ao compartilhar uma pesquisa, você está adicionando o usuário ao
        repositório do GitHub em que a pesquisa está salva. Sendo assim, na
        prática, essa pessoa terá acesso a todas as pesquisas do mesmo
        repositório.
      </small>
      <Button
        class="v--bg-amaranth"
        @click="generateLink"
        :disabled="!collaborator.length"
        :loading="$wait.is('getting-link')"
        >Gerar link de compartilhamento</Button
      >
    </div>
    <div v-else style="display: grid; grid-gap: 8px">
      <Field dark readonly name="share-link" v-model="link" />
      <small>
        OBS: Ao compartilhar uma pesquisa, você está adicionando o usuário ao
        repositório do GitHub em que a pesquisa está salva. Sendo assim, na
        prática, essa pessoa terá acesso a todas as pesquisas do mesmo
        repositório.
      </small>
    </div>
    <template v-if="link" #footer>
      <div class="v--flex-gap">
        <Button @click="removeInvitation" :disabled="!link">
          Retirar convite
        </Button>
        <Button class="v--bg-amaranth" @click="copyLink" :disabled="!link">
          Copiar link
        </Button>
      </div>
    </template>
  </Modal>
</template>
<script>
export default {
  name: 'ShareModal',
  emits: ['close'],
  props: {
    survey: {
      type: String,
      default: window.location.href
    },
    repo: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      collaborator: '',
      link: ''
    }
  },
  computed: {
    shareLink() {
      return `${this.survey}`
    }
  },
  methods: {
    copyLink() {
      this.$copyText(this.link)
        .then(() => this.$toast.success('Link copiado!'))
        .catch(() => this.$toast.error('Falha ao copiar'))
    },
    generateLink() {
      if (this.collaborator.length) {
        this.$wait.start('getting-link')
        this.$github
          .addCollaborator(this.repo, this.collaborator)
          .then(({ data }) => {
            if (!data?.id) {
              this.$toast.show('Essa pessoa já foi convidada!')
            } else {
              this.link = `${this.shareLink}&invitation=${data?.id}`
            }
          })
          .finally(() => {
            this.$wait.end('getting-link')
          })
      } else {
        this.$toast.error('Informe um nome de usuário')
      }
    },
    removeInvitation() {
      return this.$github
        .removeCollaborator(this.repo, this.collaborator)
        .then(() => {
          this.collaborator = ''
          this.link = ''
          this.$toast.success('Convite retirado!')
        })
    }
  }
}
</script>
