<template>
  <div
    class="c-open-question__answer"
    :class="{
      [`c-open-question__answer--${sentiment(answer).toLowerCase()}`]: true,
      'c-open-question__answer--loading': $wait.is(
        `changing-sentiment-${answer.line}`
      )
    }"
  >
    <p
      :class="{ 'c-open-question--hide-layers': !layers }"
      class="c-open-question__answer-text"
      v-html="answer.text"
    />
    <div v-if="!hideInteractiveItems" class="c-open-question__controls">
      <Button
        circle
        class="v--bg-white v--text-color-bright-blue"
        @click="toggleAnswers"
        v-tooltip="'Comentários'"
      >
        <Icon name="message-circle" size="14" />
      </Button>
      <Button
        circle
        :selected="layers"
        @click="toggleLayers"
        class="v--bg-white v--text-color-bright-blue"
        v-tooltip="'Marcar palavras que influenciaram no sentimento automático'"
      >
        <Icon name="layers" size="14" />
      </Button>
      <Button
        circle
        :selected="cloud"
        @click="toggleCloud"
        v-tooltip="'Wordcloud'"
        class="v--bg-white v--text-color-bright-blue"
      >
        <Icon name="word-cloud" size="14" />
      </Button>
      <Dropdown
        v-if="!encrypt"
        hide-indicator
        circle
        :change-name-on-select="false"
        :button-classes="['v--bg-white', 'v--text-color-bright-blue']"
        :items="sentimentOptions"
        @change="changeSentiment"
        v-tooltip="'Alterar sentimento'"
      >
        <Icon
          v-if="$wait.is(`changing-sentiment-${answer.line}`)"
          class="c-open-question__sentiment-loader"
          name="loader"
          size="14"
        />
        <Icon v-else name="repeat" size="14" />
      </Dropdown>
    </div>
    <template v-if="!hideInteractiveItems">
      <CommentsContainer v-if="answers">
        <Comment
          v-for="(comment, index) in commentsList"
          :comment="comment"
          :key="index"
          @delete="
            $emit('delete-comment', {
              ...$event,
              answer: answer.line,
              question: question
            })
          "
        />
      </CommentsContainer>
      <Spacing v-if="layers" top="2" bottom="1">
        <ul>
          <li>Sentimento automático: {{ sentiment(answer, true) }}</li>
          <li v-if="hasManualSentiment">
            Sentimento manualmente configurado:
            {{ sentimentPtBr(sentiment(answer)) }}
          </li>
        </ul>
      </Spacing>
      <div style="position: relative">
        <div
          v-if="cloud && percentage > 0 && percentage !== 100"
          style="
            height: 300px;
            display: flex;
            align-items: center;
            justify-content: center;
            position: absolute;
            width: 100%;
          "
        >
          <div style="width: 250px; text-align: center">
            <span style="font-size: 20px; font-weight: 700">
              {{ percentage.toFixed(2) }}%
            </span>
            <ProgressBar bar-only :percentage="percentage.toFixed(2)" />
          </div>
        </div>
      </div>
      <Spacing v-if="cloud" top="2" bottom="1">
        <VueWordCloud
          style="height: 300px; width: 100%"
          :words="words"
          :color="wordCloudColors"
          :enter-animation="{
            opacity: 0,
            transform: 'scale3d(0.3,0.3,0.3)'
          }"
          font-family="Roboto"
          @percentage="percentage = $event"
        >
          <template v-slot="{ text, weight, word }">
            <div
              :title="weight"
              style="cursor: pointer"
              @click="onWordClick(word)"
            >
              {{ text }}
            </div>
          </template>
        </VueWordCloud>
      </Spacing>
    </template>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import Comment from '@/components/pesquisa/Comment/Comment.vue'
import CommentsContainer from '@/components/pesquisa/CommentsContainer/CommentsContainer.vue'
import survey from '@/mixins/survey'

export default {
  name: 'OpenQuestionAnswer',
  emits: ['open-answer-modal'],
  mixins: [survey],
  components: {
    Comment,
    CommentsContainer
  },
  props: {
    answer: {
      type: Object,
      default: () => ({})
    },
    question: {
      type: Number,
      required: true
    },
    encrypt: {
      type: Boolean,
      default: false
    },
    hideInteractiveItems: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      percentage: 0,
      layers: false,
      cloud: false,
      answers: true
    }
  },
  computed: {
    ...mapState({
      survey: (state) => state.surveys.survey,
      coding: (state) => state.codes.codes
    }),
    commentsList() {
      if (this.encrypt) {
        return (
          [
            ...(this.encrypt &&
              (this.coding?.comments?.answers?.[this.question]?.[
                this.answer.line
              ] ||
                []))
          ].sort((a, b) => b.created_at - a.created_at) || []
        )
      }
      return (
        this.survey?.comments?.answers?.[this.question]?.[this.answer.line] ||
        []
      )
    },
    words() {
      return this.$word.count(this.answer.originalText, this.survey.language)
    },
    sentimentOptions() {
      const items = [
        { name: 'Tornar positivo', value: 'Positive' },
        { name: 'Tornar negativo', value: 'Negative' },
        { name: 'Tornar neutro', value: 'Neutral' }
      ]
      return items.filter((i) => i.value !== this.sentiment(this.answer))
    },
    hasManualSentiment() {
      return !!this.survey.sentiments?.[this.question]?.[this.answer.line]
    },
    moda() {
      const words = this.words.reduce((acc, item) => {
        acc[item[1]] = acc[item[1]] ? acc[item[1]] + 1 : 1
        return acc
      }, {})
      const values = Object.keys(words)
      const total = values.length

      let eqGt = 0

      if (total > 3) {
        eqGt = parseInt(values[total - 3])
      } else {
        eqGt = parseInt(values[total - 1])
      }

      const sortedValues = Object.keys(words).sort(
        (a, b) => words[b] - words[a]
      )
      const moda = parseInt(sortedValues[0])

      return {
        moda,
        eqGt
      }
    }
  },
  methods: {
    wordCloudColors([, weight]) {
      return weight === this.moda.moda
        ? 'Indigo'
        : weight >= this.moda.eqGt
        ? 'DeepPink'
        : 'RoyalBlue'
    },
    changeSentiment(item) {
      this.$wait.start(`changing-sentiment-${this.answer.line}`)
      this.sentiments()
        .create({
          question: this.question,
          answer: this.answer.line,
          value: item.value
        })
        .finally(() => {
          this.$wait.end(`changing-sentiment-${this.answer.line}`)
        })
    },
    sentiment(answer, translate = false) {
      if (
        this.survey.sentiments?.[this.question]?.[this.answer.line] &&
        !translate
      ) {
        return this.survey.sentiments[this.question][this.answer.line]
      }

      const sentiment =
        answer?.sentiment?.score > 1
          ? 'Positive'
          : answer?.sentiment?.score < -1
          ? 'Negative'
          : 'Neutral'

      return !translate || survey.language === 'en'
        ? sentiment
        : this.sentimentPtBr(sentiment)
    },
    sentimentPtBr(sentiment) {
      if (sentiment === 'Positive') {
        return 'Positivo'
      }
      if (sentiment === 'Negative') {
        return 'Negativo'
      }
      return 'Neutro'
    },
    toggleCloud() {
      this.cloud = !this.cloud
      if (this.layers) {
        this.layers = false
      }
      if (this.answers) {
        this.answers = false
      }
    },
    toggleLayers() {
      this.layers = !this.layers
      if (this.cloud) {
        this.cloud = false
        this.percentage = 0
      }
      if (this.answers) {
        this.answers = false
      }
    },
    toggleAnswers() {
      this.$emit('open-answer-modal')
      if (!this.answers) {
        this.answers = true
      }
      if (this.cloud) {
        this.cloud = false
        this.percentage = 0
      }
      if (this.layers) {
        this.layers = false
      }
    }
  }
}
</script>
<style lang="stylus" src="./style.styl" scoped />
