<template>
  <PesquisaLoading v-show="$wait.is('mounting') && survey?.questions?.length" />

  <div v-if="!$wait.is('mounting')">
    <DropFile
      label="Arquivo com as perguntas do questionário"
      :disabled="$wait.is('creating-pilot')"
      @file="file = $event"
      @load="onLoad"
    />
    <template v-if="questions && questions.length">
      <Spacing class="v--flex-gap" vertical="2">
        <Tag class="v--bg-oxford-blue"
          >{{ classifications.length }} Questões</Tag
        >
        <Tag class="v--bg-oxford-blue"
          >{{ openQuestions.length }} Questões abertas</Tag
        >
        <Tag class="v--bg-oxford-blue"
          >{{ closedQuestions.length }} Questões fechadas</Tag
        >
      </Spacing>
      <Spacing vertical="2">
        <p>
          Marque as questões com as tags "Deve saber", "Útil saber" e "Bom
          saber".
        </p>
        <p>
          Questões marcadas como "Bom saber" normalmente são questões sem
          objetivo claro na pesquisa, evite-as.
        </p>
      </Spacing>
      <QuestionsContainer>
        <template v-for="(question, index) in questions" :key="index">
          <Question
            :ref="`question-${index}`"
            :question="question"
            :only-statements="true"
            hide-answer
            kind="pilot"
          />
        </template>
      </QuestionsContainer>
    </template>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { QuestionClassifier } from '@/plugins/question-classifier'
import Question from '@/components/pesquisa/Question/Question.vue'
import QuestionsContainer from '@/components/pesquisa/QuestionsContainer/QuestionsContainer.vue'
import { format } from 'date-fns'
import PesquisaLoading from '@/components/pesquisa/Loading/Loading.vue'
import survey from '@/mixins/survey'

export default {
  name: 'Pilot',
  components: {
    Question,
    QuestionsContainer,
    PesquisaLoading
  },
  mixins: [survey],
  data() {
    return {
      file: null,
      load: null,
      form: {
        repository: 'https://github.com/jprodrigues70/tese-lyz',
        name: 'dataset_' + format(new Date(), 'dd-MM-Y_HH-mm-ss'),
        language: 'ptBr'
      },
      completionList: [],
      whatDoing: ''
    }
  },
  computed: {
    ...mapState({
      survey: (state) => state.surveys.survey,
      pilot: (state) => state.pilots.pilot
    }),
    options() {
      return [
        { name: 'Português', value: 'ptBr' },
        { name: 'Inglês', value: 'en' }
      ]
    },
    classifications() {
      return this.pilot?.classifications || []
    },
    openQuestions() {
      return this.classifications.filter((i) => this.isOpenQuestion(i))
    },
    closedQuestions() {
      return this.classifications.filter((i) => this.isClosedQuestion(i))
    },
    questions() {
      const classifier = new QuestionClassifier()
      return this.pilot?.classifications?.map((classification, index) => {
        return {
          title: this.pilot.questions[index],
          classification,
          type: classifier.getType(classification),
          column: index
        }
      })
    },
    githubLink() {
      return `https://github.com/${this.$route.params.org}/${this.$route.params.repo}`
    }
  },
  created() {
    this.$wait.start('mounting')
    this.pilots()
      .get()
      .finally(() => {
        this.$wait.end('mounting')
      })
  },
  methods: {
    onLoad(load) {
      this.load = load
      this.submit()
    },
    submit() {
      if (!this.load || !this.load.data) {
        this.$toast.error('Você precisa adicionar algum arquivo!')
        return
      }
      this.$wait.start('creating-pilot')

      this.whatDoing = 'Criando base de dados'

      if (this.pilot && Object.keys(this.pilot).length) {
        return this.readFile()
          .then((pilot) => {
            return this.pilots().update(pilot, 'New Pilot')
          })
          .finally(() => {
            this.$wait.end('creating-pilot')
          })
      } else {
        return this.readFile()
          .then((pilot) => {
            this.createPilot(pilot, 'New Pilot')
          })
          .finally(() => {
            this.$wait.end('creating-pilot')
          })
      }
    },
    isOpenQuestion(item) {
      const open = ['feedback', 'negativeOrList', 'openQuestion']
      return open.includes(item)
    },
    isClosedQuestion(item) {
      const closed = [
        'cityState',
        'groupsInAnswer',
        'Comma-separated groups in answer',
        'npsLike',
        'terms',
        'yesOrNot'
      ]
      return closed.includes(item)
    },
    readFile() {
      this.whatDoing = 'Lendo o arquivo'
      return this.$csvReader.read(this.file).then((data) => {
        this.whatDoing = 'Classificando os enunciados'
        const questions = data.shift()

        const classifications = this.$questionClassifier.classify(
          questions,
          this.form.language
        )

        const dataset = {
          language: this.form.language,
          classifications,
          questions,
          manualSettings: {},
          comments: {},
          tags: {
            list: [
              {
                name: 'Deve Saber',
                color: '109121'
              },
              {
                name: 'Útil saber',
                color: '37b748'
              },
              {
                name: 'Bom saber',
                color: '7ccb86'
              },
              {
                name: 'Demografia',
                color: '8930e8'
              }
            ],
            questions: classifications.reduce((acc, item, index) => {
              const demographics = ['cityState']
              const feedback = ['feedback']
              if (demographics.includes(item)) {
                acc[index] = [
                  {
                    name: 'Demografia',
                    color: '8930e8'
                  }
                ]
              }

              if (feedback.includes(item)) {
                acc[index] = [
                  {
                    name: 'Bom saber',
                    color: '7ccb86'
                  }
                ]
              }

              return acc
            }, {})
          }
        }

        return Promise.resolve(dataset)
      })
    },
    createPilot(dataset) {
      this.whatDoing = 'Salvando conteúdo no GitHub'

      return this.$github
        .commit(
          {
            message: 'Create Pilot',
            content: JSON.stringify(dataset)
          },
          this.githubLink,
          `database/${this.$route.params.file}/pilot.json`
        )
        .then(() => {
          this.pilots().get()
        })
        .catch(({ response }) => {
          if (
            response.data.message ===
            'Invalid request.\n\n"sha" wasn\'t supplied.'
          ) {
            this.$toast.error('Já existe um arquivo com esse nome!')
          }
        })
        .finally(() => {
          this.$wait.end('creating-survey')
        })
    }
  }
}
</script>
