<template>
  <div class="c-invitation-card">
    <div class="c-invitation-card__content">
      <h2 class="c-invitation-card__title">
        {{ invitation?.repository?.name }}
      </h2>
      <p class="c-invitation-card__subtitle">
        <a :href="invitation?.repository?.html_url" target="_blank">
          {{ invitation?.repository?.full_name }}
        </a>
      </p>
      <p>Por: {{ invitation?.inviter?.login }}</p>
    </div>

    <Button v-if="invitation?.expired" class="v--bg-oxford-blue" disabled>
      Convite expirado
    </Button>
    <Button v-else class="v--bg-lyzeli-yellow" @click="accept">
      Aceitar Convite
    </Button>
  </div>
</template>
<script>
export default {
  name: 'InitationCard',
  props: {
    invitation: {
      type: Object,
      default: () => ({})
    }
  },
  methods: {
    accept() {
      this.$github
        .acceptInvitation(this.invitation?.id)
        .then(() => {
          this.$router.push('/inicio')
        })
        .then(() => {
          this.$toast.success('Convite aceito!')
        })
        .catch(() => {
          this.$toast.error('Falha ao aceitar convite!')
        })
    }
  }
}
</script>
<style src="./style.styl" lang="stylus" scoped />
