<template>
  <PesquisaLoading v-show="$wait.is('mounting') && survey?.questions?.length" />
  <div v-if="!survey?.questions?.length && !$wait.is('survey')">
    <Spacing top="2">
      <DropFile
        label="Para ver os resultados, adicione um arquivo com as perguntas e respostas do Survey"
        :disabled="$wait.is('creating-survey')"
        @file="file = $event"
        @load="onLoad"
      />
    </Spacing>
    <Spacing :horizontal="1 / 2">
      <p>
        <small>
          Se você utilizou o Google Forms, você pode
          <a
            href="https://support.google.com/merchants/answer/160569?hl=pt-BR"
            target="_blank"
            rel="noreferrer"
          >
            ver como exportar seus dados em formato .tsv aqui.
          </a>
        </small>
      </p>
    </Spacing>
    <Spacing :horizontal="1 / 2" vertical="2">
      <div class="p-new__label">
        Caso queira remover alguma questão antes de salvar, clique no botão
        abaixo.
      </div>
      <Spacing v-if="questions.length" top="1">
        <div class="v--flex v--flex-vertical v--flex-gap">
          <p>
            <small>
              <Tag class="v--bg-success--light">
                {{ questions.length - removedQuestions.length }} questões serão
                salvas.
              </Tag>
            </small>
          </p>
          <p>
            <small v-if="removedQuestions.length">
              <Tag class="v--bg-fail--light">
                A planilha original tem {{ questions.length }} questões, mas
                você escolheu não incluir {{ removedQuestions.length }} delas.
              </Tag>
            </small>
          </p>
        </div>
      </Spacing>
      <Spacing vertical="1">
        <Button
          v-tooltip="'Selecionar as questões que deseja salvar'"
          type="button"
          with-icon
          :disabled="!questions.length"
          @click="showQuestions = true"
        >
          <Icon name="check-square" :size="14" />
          Selecionar questões para salvar
        </Button>
      </Spacing>
    </Spacing>

    <Spacing top="2">
      <Button
        class="v--bg-amaranth"
        type="submit"
        :loading="$wait.is('creating-survey')"
        :disabled="$wait.is('creating-survey')"
      >
        Salvar dados dos resultados da pesquisa
      </Button>
    </Spacing>
  </div>
  <div
    v-if="!$wait.is('survey')"
    class="c-results"
    :style="{ opacity: $wait.is('mounting') ? 0 : 1 }"
  >
    <Toolbar
      class="c-results__header"
      @change-template="template = $event"
      @filter-cards="filter"
      @relational-filter="showRelationalFilterModal = true"
    />
    <QuestionsContainer class="c-results__main">
      <template v-if="allowed">
        <Question
          v-for="(question, index) in questionsList"
          :ref="`question-${question.column}`"
          :key="index"
          :question="question"
          :open="template === 'grid'"
          :loading="$wait.is(`changing-question-${question.column}`)"
          @do-comment="$emit('comment-on-question', question)"
          @show-answers="showAnswers"
          @show-latex="showLatexModal"
          @change-class="$emit('change-class', $event)"
          @loaded="loaded = index"
          @remove-comment="$emit('remove-comment', $event)"
          @show-charts="showChartsModal = $event"
          @count-words="showCountWordsModal = $event"
        />
      </template>
    </QuestionsContainer>
    <Modal v-if="latex" title="Código LaTeX" @close="latex = null">
      <LatexChart :total="latex.total" :set="latex.set" />
    </Modal>
    <SubmissionModal
      v-if="showingAnswers"
      :questions="questions"
      :showing-answers="showingAnswers"
      @archive="archive"
      @close="showingAnswers = []"
    />
    <RelationalFilter
      v-if="showRelationalFilterModal"
      :last-filters="filters"
      :questions="questions"
      @close="showRelationalFilterModal = false"
      @choose-relational-filter="applyRelationalFilter"
    />
    <SelectionBar
      v-if="hasRelationalFilter"
      @relational-filter="showRelationalFilterModal = true"
      @clear="applyRelationalFilter({})"
    />
    <ChartsModal
      v-if="showChartsModal"
      :question="showChartsModal"
      @close="showChartsModal = null"
    />
    <Modal
      v-if="showCountWordsModal"
      title="Contador de palavras"
      medium
      @close="showCountWordsModal = null"
    >
      <p v-for="word in showCountWordsModal" :key="word[0]">
        {{ word[0] }}: {{ word[1] }}
      </p>
    </Modal>
  </div>
  <PesquisaLoading class="c-results__main" v-else />
</template>
<script>
import { mapState } from 'vuex'
import Toolbar from '@/components/pesquisa/Results/Toolbar/Toolbar.vue'
import Question from '@/components/pesquisa/Question/Question.vue'
import QuestionsContainer from '@/components/pesquisa/QuestionsContainer/QuestionsContainer.vue'
import PesquisaLoading from '@/components/pesquisa/Loading/Loading.vue'
import SubmissionModal from '@/components/pesquisa/Submissions/SubmissionModal/SubmissionModal.vue'
import survey from '@/mixins/survey'
import RelationalFilter from '@/components/pesquisa/RelationalFilter/RelationalFilter.vue'
import ChartsModal from '@/components/pesquisa/Question/Charts/Charts.vue'

export default {
  name: 'SurveyResults',
  components: {
    Toolbar,
    Question,
    ChartsModal,
    PesquisaLoading,
    QuestionsContainer,
    SubmissionModal,
    RelationalFilter
  },
  props: {
    questions: {
      type: Array,
      default: () => []
    }
  },
  mixins: [survey],
  emits: [
    'change-class',
    'update-survey-data',
    'comment-on-question',
    'remove-comment'
  ],
  data() {
    return {
      latex: null,
      filterByType: null,
      showingAnswers: [],
      questionsToShow: [],
      template: localStorage.getItem('questions-template') || 'list',
      loaded: null,
      allowed: false,
      mounting: false,
      showRelationalFilterModal: false,
      filters: {},
      showChartsModal: null,
      showCountWordsModal: null
    }
  },
  beforeMount() {
    this.questionsToShow = []
    this.$wait.start('mounting')
  },
  mounted() {
    setTimeout(() => {
      this.allowed = true
    }, 100)
  },
  computed: {
    ...mapState({
      survey: (state) => state.surveys.survey,
      filtereds: (state) => state.answers.filtereds
    }),
    hasRelationalFilter() {
      return Object.keys(this.filters).length
    },
    questionsList() {
      if (this.filters && Object.keys(this.filters).length) {
        let lines = []
        Object.keys(this.filters).forEach((key, index) => {
          if (index === 0) {
            lines = [...this.filters[key].lines]
          } else {
            lines = lines.filter((i) => this.filters[key].lines.includes(i))
          }
        })
        return this.questions.map((question) => {
          const answers = question.answers.filter((answer) => {
            return lines.includes(answer.line)
          })
          return { ...question, answers }
        })
      }
      if (this.filterByType?.value) {
        return this.questions.filter((i) => i.type === this.filterByType.value)
      }
      return this.questions
    }
  },
  watch: {
    loaded(newer) {
      if (newer === this.questionsList.length - 1) {
        this.$wait.end('mounting')
      }
      if (newer === 0) {
        this.$wait.start('mounting')
      }
    },
    questionsList() {
      this.$wait.end('mounting')
    }
  },
  beforeUnmount() {
    this.loaded = 0
  },
  methods: {
    showAnswers(answers) {
      this.showingAnswers = answers
      this.currentLine = 0
    },
    showLatexModal(e) {
      this.latex = e
    },
    filter(event) {
      this.filterByType = event
    },
    archive(line) {
      this.$wait.start('archive-submission-status')
      const filtereds = [...(this.filtereds || []), line]
      this.showingAnswers = this.showingAnswers.filter((i) => i !== line)
      this.surveys()
        .update({ filtereds }, 'Add filtered')
        .then((survey) => {
          this.$emit('update-survey-data', survey)
          this.$toast.success(`Resposta arquivada`)
        })
        .finally(() => {
          this.$wait.end('archive-submission-status')
        })
    },
    applyRelationalFilter(filters) {
      this.filters = { ...filters }
      if (Object.keys(filters).length) {
        this.$toast.success('Filtro aplicado!')
      } else {
        this.$toast.show('Nenhum filtro aplicado!')
      }
      this.showRelationalFilterModal = false
    }
  }
}
</script>
<style lang="stylus" src="./style.styl" scoped />
