<template>
  <div class="p-file">
    <Spacing class="p-file__title-bar" bottom="4">
      <Spacing right="1">
        <GoBackButton :to="'/inicio'" />
      </Spacing>
      <div style="width: 100%">
        <div class="p-file__title-bar-content">
          <div class="p-file__title-header">
            <h1 class="p-file__title">Convites</h1>
            <small>
              Aqui você vai ver os convites para participar de outras pesquisas.
            </small>
          </div>
        </div>
      </div>
    </Spacing>
    <Card
      v-for="invitation in invitations"
      :key="invitation.id"
      :invitation="invitation"
    />
  </div>
</template>
<script>
import { mapState } from 'vuex'
import Card from '@/components/invitations/Card/Card.vue'

export default {
  name: 'invitations',
  route: '/convites',
  layout: 'painel',
  components: {
    Card
  },
  computed: {
    ...mapState({
      user: (state) => state.auth.user,
      invitations: (state) => state.auth.invitations
    })
  },
  beforeCreate() {
    this.$github.getInvitations().then(({ data }) => {
      this.$store.commit('auth/setInvitations', data)
    })
  },
  methods: {
    login() {
      this.$wait.start('login')
      this.$github
        .login()
        .then(() => this.$router.push('/inicio'))
        .finally(() => this.$wait.end('login'))
    }
  }
}
</script>
