<template>
  <div class="c-pesquisa-form">
    <div class="c-pesquisa-form__menu">
      <Button
        :selected="isActive('fundamentacao')"
        @click="changeEtapa('fundamentacao')"
      >
        1- Fundamentação
      </Button>
      <Button
        :selected="isActive('amostragem')"
        @click="changeEtapa('amostragem')"
      >
        2- Plano de amostragem
      </Button>
      <Button
        :selected="isActive('questionario')"
        @click="changeEtapa('questionario')"
      >
        3- Plano do questionário
      </Button>
      <Button
        :selected="isActive('questionario-analise')"
        @click="changeEtapa('questionario-analise')"
      >
        4- Análise do questionário
      </Button>
    </div>
    <form
      v-if="isActive('fundamentacao')"
      class="c-pesquisa-form__form"
      @submit.prevent
    >
      <Field
        name="title"
        label="Título"
        v-model="form.fundamentacao.title"
        :value="survey?.information?.title"
      />
      <FieldText
        name="objective"
        label="Objetivos"
        :sublabel="objective.sublabel"
        rows="7"
        v-model="form.fundamentacao.objective"
        :value="survey?.information?.objective"
      />
      <Field
        name="target"
        label="Público alvo"
        :sublabel="target.sublabel"
        v-model="form.fundamentacao.target"
        :value="survey?.information?.target"
      />
      <FieldText
        name="about-target"
        label="Características do público alvo"
        rows="7"
        :sublabel="aboutTarget.sublabel"
        v-model="form.fundamentacao.aboutTarget"
        :value="survey?.information?.aboutTarget"
      />
      <div>
        <Button
          class="v--bg-amaranth"
          sprawled
          type="button"
          :loading="$wait.is('fundamentacao')"
          @click="save('fundamentacao')"
        >
          Salvar
        </Button>
      </div>
    </form>
    <form v-if="isActive('amostragem')" class="c-pesquisa-form__form">
      <FieldText
        name="aquisition"
        label="Como as pessoas serão selecionadas para participar da pesquisa?"
        v-model="form.amostragem.aquisition"
        :value="survey?.information?.aquisition"
      />
      <Field
        name="size"
        label="Qual é o tamanho mínimo da amostra?"
        :sublabel="sampleSize.sublabel"
        v-model="form.amostragem.sampleSize"
        :value="survey?.information?.sampleSize"
      />
      <Label label="Selecione o tipo de amostragem" name="sample" />
      <Spacing vertical="1" style="display: grid; grid-gap: 8px">
        <ToggleField
          name="sample"
          type="radio"
          radio-id="sample-a"
          label="Probabilística"
          :sublabel="sample.sublabel.probability"
          v-model="form.amostragem.sampleType"
          value="Probabilística"
          :checked="'Probabilística' === survey?.information?.sampleType"
        />
        <ToggleField
          radio-id="sample-b"
          name="sample"
          type="radio"
          label="Não-probabilística"
          :sublabel="sample.sublabel.nonProbability"
          v-model="form.amostragem.sampleType"
          value="Não-probabilística"
          :checked="'Não-probabilística' === survey?.information?.sampleType"
        />
      </Spacing>
      <Spacing top="2">
        <Button
          class="v--bg-amaranth"
          sprawled
          type="button"
          :loading="$wait.is('amostragem')"
          @click="save('amostragem')"
        >
          Salvar
        </Button>
      </Spacing>
    </form>
    <form v-if="isActive('questionario')" class="c-pesquisa-form__form">
      <Field
        name="address"
        label="Endereço web do questionário"
        v-model="form.questionario.address"
        :value="survey?.information?.address"
      />
      <Field
        name="time"
        label="Tempo em que o questionário deve ficar esperando respostas"
        :sublabel="time.sublabel"
        v-model="form.questionario.time"
        :value="survey?.information?.time"
      />
      <Field
        name="answering-time"
        label="Tempo estimado de resposta"
        :sublabel="answeringTime.sublabel"
        v-model="form.questionario.answeringTime"
        :value="survey?.information?.answeringTime"
      />

      <Spacing top="2">
        <Button
          class="v--bg-amaranth"
          sprawled
          type="button"
          :loading="$wait.is('questionario')"
          @click="save('questionario')"
        >
          Salvar
        </Button>
      </Spacing>
    </form>
    <div v-if="isActive('questionario-analise')">
      <Pilot />
    </div>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import Pilot from '@/components/pesquisa/Pilot/Pilot.vue'
import github from '@/mixins/github'
export default {
  name: 'PesquisaForm',
  mixins: [github],
  components: { Pilot },
  data() {
    return {
      form: {
        fundamentacao: {
          title: this.survey?.information?.title,
          objective: this.survey?.information?.objective,
          target: this.survey?.information?.target,
          aboutTarget: this.survey?.information?.aboutTarget
        },
        questionario: {
          address: this.survey?.information?.address,
          time: this.survey?.information?.time,
          answeringTime: this.survey?.information?.answeringTime
        },
        amostragem: {
          aquisition: this.survey?.information?.aquisition,
          sampleSize: this.survey?.information?.sampleSize,
          sampleType: this.survey?.information?.sampleType
        }
      }
    }
  },
  computed: {
    ...mapState({ survey: (state) => state.surveys.survey }),
    objective() {
      return {
        sublabel:
          'Os objetivos do Survey fornecem escopo e orientação para perguntas internas que a equipe tem sobre o problema ou questão.'
      }
    },
    target() {
      return {
        sublabel:
          'Para os objetivos de pesquisa que você tem, quem pode melhor fornecer as informações de que você precisa?'
      }
    },
    aboutTarget() {
      return {
        sublabel:
          'Identificar as características do seu público alvo vai te ajudar a elaborar perguntas mais acertivas e a entender melhor as respostas obtidas.'
      }
    },
    sample() {
      return {
        sublabel: {
          probability:
            'Os resultados da pesquisa serão generalizados para uma população maior',
          nonProbability:
            'Os resultados da pesquisa serão considerados exclusivos para os indivíduos que participarem da pesquisa.'
        }
      }
    },
    sampleSize() {
      return {
        sublabel:
          'Determine o tamanho apropriado da amostra em função da precisão desejada, do nível de confiança desejado, e do tamanho da população.'
      }
    },
    time() {
      return {
        sublabel:
          'Um questionário deve ficar aberto por um período pré-determinado de tempo'
      }
    },
    answeringTime() {
      return {
        sublabel:
          'Quanto tempo uma pessoa vai levar, em média, para responder todas as perguntas do questionário?'
      }
    }
  },
  methods: {
    changeEtapa(etapa) {
      const query = {
        ...this.$route.query,
        etapa
      }

      this.$router.replace({
        ...this.$route,
        query
      })
    },
    isActive(key) {
      return this.$route.query?.etapa === key
    },
    save(key) {
      this.$wait.start(key)
      const survey = this.survey
      survey.information = {
        ...this.survey.information,
        ...this.form[key]
      }

      console.log(this.form)

      this.updateSurvey(survey, `Update ${key}`)
        .then(() => {
          this.$toast.success('Informações atualizadas!')
          return this.$store.commit('surveys/setSurvey', survey)
        })
        .finally(() => {
          this.$wait.end(key)
        })
    }
  }
}
</script>
<style src="./style.styl" lang="stylus" scoped />
