<template>
  <div class="p-new">
    <Spacing bottom="2">
      <Button :to="$route.params.origem || '/inicio'" circle>
        <Icon name="arrow-left" />
      </Button>
    </Spacing>
    <h1 class="p-new__title">Criar pesquisa</h1>
    <p v-if="!$route.query.tipo">
      Selecione o modo como deseja criar sua pesquisa.
    </p>
    <Loader v-if="$wait.is('creating-survey')" :what-doing="whatDoing" />
    <Spacing top="4" v-if="!$route.query.tipo">
      <div class="p-new__cards">
        <div class="p-new__card" @click="selectType('upload')">
          <div class="p-new__card-icon v--bg-lyzeli-blue">
            <Icon name="upload-cloud" size="50" />
          </div>
          <div class="p-new__card-text">
            <span class="p-new__card-title">
              Fazer upload da planilha de respostas primeiro
            </span>
            <p class="p-new__card-description">
              Você irá adicionar as respostas do Survey primeiro, e poderá
              adicionar as informações da pesquisa quando quiser. Mais útil para
              quem já aplicou o Survey.
            </p>
          </div>
        </div>
        <div class="p-new__card" @click="selectType('informacoes')">
          <div class="p-new__card-icon v--bg-lyzeli-yellow">
            <Icon name="list" size="50" />
          </div>
          <div class="p-new__card-text">
            <span class="p-new__card-title">
              Adicionar informações sobre o Survey primeiro
            </span>
            <p class="p-new__card-description">
              Você irá adicionar as informações sobre o Survey primeiro, e
              poderá fazer upload das respostas quando quiser. Mais útil para
              quem está construindo o Survey.
            </p>
          </div>
        </div>
      </div>
    </Spacing>
    <Spacing top="4" v-if="$route.query.tipo === 'upload'">
      <form @submit.prevent="submit">
        <Spacing vertical="2">
          <Field
            required
            label="Repositório do GitHub onde os dados serão salvos"
            sublabel="Dê preferência a repositórios privados para assegurar a segurança de seus dados."
            name="repository"
            v-model="form.repository"
            placeholder="Ex: https://github.com/group/project"
            :disabled="$wait.is('creating-survey')"
          />
        </Spacing>
        <Spacing vertical="2">
          <Field
            required
            label="Título da Pesquisa"
            name="name"
            v-model="form.name"
            :disabled="$wait.is('creating-survey')"
          />
        </Spacing>
        <Spacing vertical="2">
          <FieldSelect
            required
            name="language"
            label="Língua em que os dados estão"
            :options="options"
            :disabled="$wait.is('creating-survey')"
            @change="form.language = $event.target.value"
          />
        </Spacing>
        <Spacing vertical="2">
          <DropFile
            label="Arquivo com as perguntas e respostas do Survey"
            :disabled="$wait.is('creating-survey')"
            @file="file = $event"
            @load="onLoad"
          />
        </Spacing>
        <Spacing :horizontal="1 / 2">
          <p>
            <small>
              Se você utilizou o Google Forms, você pode
              <a
                href="https://support.google.com/merchants/answer/160569?hl=pt-BR"
                target="_blank"
                rel="noreferrer"
              >
                ver como exportar seus dados em formato .tsv aqui.
              </a>
            </small>
          </p>
        </Spacing>
        <Spacing :horizontal="1 / 2" vertical="2">
          <div class="p-new__label">
            Caso queira remover alguma questão antes de salvar, clique no botão
            abaixo.
          </div>
          <Spacing v-if="questions.length" top="1">
            <div class="v--flex v--flex-vertical v--flex-gap">
              <p>
                <small>
                  <Tag class="v--bg-success--light">
                    {{ questions.length - removedQuestions.length }} questões
                    serão salvas.
                  </Tag>
                </small>
              </p>
              <p>
                <small v-if="removedQuestions.length">
                  <Tag class="v--bg-fail--light">
                    A planilha original tem {{ questions.length }} questões, mas
                    você escolheu não incluir
                    {{ removedQuestions.length }} delas.
                  </Tag>
                </small>
              </p>
            </div>
          </Spacing>
          <Spacing vertical="1">
            <Button
              v-tooltip="'Selecionar as questões que deseja salvar'"
              type="button"
              with-icon
              :disabled="!questions.length"
              @click="showQuestions = true"
            >
              <Icon name="check-square" :size="14" />
              Selecionar questões para salvar
            </Button>
          </Spacing>
        </Spacing>

        <Spacing top="2">
          <Button
            class="v--bg-amaranth"
            type="submit"
            :loading="$wait.is('creating-survey')"
            :disabled="$wait.is('creating-survey')"
          >
            Criar pesquisa
          </Button>
        </Spacing>
      </form>
    </Spacing>
    <Spacing top="4" v-if="$route.query.tipo === 'informacoes'">
      <form @submit.prevent="submit">
        <Spacing vertical="2">
          <Field
            required
            label="Repositório do GitHub onde os dados serão salvos"
            sublabel="Dê preferência a repositórios privados para assegurar a segurança de seus dados."
            name="repository"
            v-model="form.repository"
            placeholder="Ex: https://github.com/group/project"
            :disabled="$wait.is('creating-survey')"
          />
        </Spacing>
        <Spacing vertical="2">
          <Field
            required
            label="Título da Pesquisa"
            name="name"
            v-model="form.name"
            :disabled="$wait.is('creating-survey')"
          />
        </Spacing>
        <Spacing vertical="2">
          <FieldSelect
            required
            name="language"
            label="Língua em que o questionário estará"
            :options="options"
            :disabled="$wait.is('creating-survey')"
            @change="form.language = $event.target.value"
          />
        </Spacing>

        <Spacing top="2">
          <Button
            class="v--bg-amaranth"
            type="submit"
            :loading="$wait.is('creating-survey')"
            :disabled="$wait.is('creating-survey')"
          >
            Criar pesquisa
          </Button>
        </Spacing>
      </form>
    </Spacing>
    <Modal
      v-if="showQuestions"
      medium
      box
      @close="showQuestions = false"
      title="
        As questões selecionadas serão salvas junto com as respostas
        relacionadas a elas. Deseja remover alguma?"
    >
      <Spacing vertical="2">
        <Spacing v-for="(question, key) in questions" :key="key" vertical="1">
          <label
            class="v--flex v--flex-gap v--flex-align-top"
            style="cursor: pointer"
          >
            <input
              type="checkbox"
              style="margin-top: 3px"
              :checked="!removedQuestions.includes(key)"
              :value="key"
              @change="changeQuestion(key)"
            />
            <div>
              <span>
                {{ question }}
              </span>
            </div>
          </label>
        </Spacing>
      </Spacing>
      <Spacing>
        <Button class="v--bg-lyzeli-blue" @click="showQuestions = false">
          Finalizar seleção
        </Button>
      </Spacing>
    </Modal>
  </div>
</template>
<script>
import { defineComponent } from 'vue'
import { format } from 'date-fns'
import { mapState } from 'vuex'
import survey from '@/mixins/survey'

export default defineComponent({
  name: 'NewSurvey',
  layout: 'painel',
  mixins: [survey],
  data() {
    return {
      file: null,
      load: null,
      form: {
        repository: '',
        // repository: 'https://github.com/jprodrigues70/tese-lyz',
        name: '',
        // name: 'dataset_' + format(new Date(), 'dd-MM-Y_HH-mm-ss'),
        language: 'ptBr'
      },
      completionList: [],
      whatDoing: '',
      questions: [],
      answers: [],
      showQuestions: false,
      removedQuestions: []
    }
  },
  computed: {
    ...mapState({
      user: (state) => state.auth.user
    }),
    options() {
      return [
        { name: 'Português', value: 'ptBr' },
        { name: 'Inglês', value: 'en' }
      ]
    }
  },
  mounted() {
    this.surveys().test()
  },
  methods: {
    changeQuestion(key) {
      const item = this.removedQuestions.find((i) => {
        return i === key
      })

      if (item !== undefined) {
        this.removedQuestions = this.removedQuestions.filter((i) => i !== key)
      } else {
        this.removedQuestions.push(key)
      }
    },
    selectType(type) {
      const etapa = type === 'informacoes' ? 'fundamentacao' : null
      this.$router.replace({
        ...this.$route,
        query: {
          ...this.$route.query,
          tipo: type,
          etapa
        }
      })
    },
    onLoad(load) {
      this.load = load
      this.readFile()
    },
    submit() {
      if (
        (!this.load || !this.load.data) &&
        this.$route.query.tipo !== 'informacoes'
      ) {
        this.$toast.error('Você precisa adicionar algum arquivo!')
        return
      }
      this.$wait.start('creating-survey')

      this.whatDoing = 'Criando base de dados'

      return this.$github
        .getFile(this.form.repository, 'database')
        .then((res) => {
          const hasDatabase = res.data.find(
            (file) => file.path === 'database/.lyzeli'
          )

          if (!hasDatabase) {
            return this.createDatabase()
              .then(() => this.uploadFile())
              .finally(() => {
                this.$wait.end('creating-survey')
              })
          } else {
            return this.uploadFile().finally(() => {
              this.$wait.end('creating-survey')
            })
          }
        })
        .catch(() => {
          return this.createDatabase()
            .then(() => this.uploadFile())
            .finally(() => {
              this.$wait.end('creating-survey')
            })
        })
    },
    createDatabase() {
      return this.$github
        .commit(
          {
            message: 'Create database',
            content: format(new Date(), 'd-MM-Y HH:mm:ss')
          },
          this.form.repository,
          'database/.lyzeli'
        )
        .then(() => {
          return this.$github.commit(
            {
              message: `Add coding`,
              content: JSON.stringify({})
            },
            this.form.repository,
            `database/coding/${this.user.id}`
          )
        })
    },
    readFile() {
      this.whatDoing = 'Lendo a planilha de dados'

      return this.$csvReader.read(this.file).then((data) => {
        this.whatDoing = 'Classificando os enunciados'
        this.questions = data.shift()
        this.answers = data
        // return this.uploadFile(questions, data)
      })
    },
    uploadFile() {
      const questions = this.questions.filter((i, key) => {
        return !this.removedQuestions.includes(key)
      })
      const answers = this.answers.map((page) => {
        return page.filter((i, key) => !this.removedQuestions.includes(key))
      })

      const classifications = this.$questionClassifier.classify(
        questions,
        this.form.language
      )

      const dataset = {
        language: this.form.language,
        classifications,
        questions,
        answers,
        manualSettings: {},
        comments: {},
        tags: {},
        information: {
          title: this.form.name
        }
      }

      this.whatDoing = 'Salvando conteúdo no GitHub'

      return this.$github
        .commit(
          {
            message: 'Create ' + this.form.name,
            content: JSON.stringify(dataset)
          },
          this.form.repository,
          `database/${this.form.name}/survey.json`
        )
        .then(() => {
          const settings = {
            ...this.form,
            survey: dataset
          }

          return this.$github
            .addRepo(settings)
            .then((settings) => {
              return this.$store
                .dispatch('auth/createSettings', settings)
                .then(() => {
                  this.whatDoing = 'Concluído!'
                  this.$router.push('/inicio')
                })
            })
            .catch((e) => console.log(e))
        })
        .catch((e) => {
          if (
            e?.response?.data.message ===
            'Invalid request.\n\n"sha" wasn\'t supplied.'
          ) {
            this.$toast.error('Já existe um arquivo com esse nome!')
          }
        })
        .finally(() => {
          this.$wait.end('creating-survey')
        })
    }
  }
})
</script>
