<template>
  <div class="c-drop-file">
    <slot />

    <label v-if="label" class="c-drop-file__label">
      {{ label }}
    </label>
    <Spacing
      v-if="file"
      vertical="2"
      class="v--flex v--flex-align-middle v--flex-gap"
    >
      <Icon name="file" size="40" style="color: #188038" />
      <div class="v--flex v--flex-vertical">
        <span>
          <b>{{ file?.name }}</b>
        </span>
        <small>
          {{ file && file.name.split('.').pop().toUpperCase() }} -
          {{ file && size(file.size) }}
        </small>
      </div>
    </Spacing>
    <div id="dropzone">
      <div
        class="c-drop-file__container v--flex v--flex-align-center v--flex-align-middle v--flex-vertical v--flex-gap"
        :class="{ 'c-drop-file__container--is-active': dropAreaIsActive }"
        @drop="dropHandler"
        @dragover="dragOverHandler"
        @dragleave="dragLeaveHandler"
      >
        <FileUpload
          :required="!file"
          placeholder="Clique aqui para escolher um arquivo .tsv ou .xlsx"
          :button-class="{ 'v--bg-lyzeli-blue': true }"
          :accept="accept"
          :max-size="maxSize"
          :options="options"
          :disabled="disabled"
          hide-file-info
          @file="addFile"
        />
        <p>OU</p>
        <p>Arraste um arquivo para esta área</p>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'DropFile',
  props: {
    label: {
      type: String,
      default: ''
    },
    options: {
      type: Array,
      default: () => []
    },
    disabled: {
      type: Boolean,
      default: false
    },
    accept: {
      type: String,
      default: '.tsv, .xlsx'
    },
    maxSize: {
      type: Number,
      default: 0 // megabytes
    }
  },
  data() {
    return {
      file: null,
      dropAreaIsActive: false
    }
  },
  methods: {
    dragOverHandler($event) {
      $event.preventDefault()
      this.dropAreaIsActive = true
    },
    dragLeaveHandler() {
      this.dropAreaIsActive = false
    },
    dropHandler($event) {
      // Impedir o comportamento padrão (impedir que o arquivo seja aberto)
      $event.preventDefault()
      this.dropAreaIsActive = false
      let file = null
      if ($event.dataTransfer.items) {
        // Se os itens soltos não forem arquivos, rejeite-os
        if ($event.dataTransfer?.items?.[0]?.kind === 'file') {
          file = $event.dataTransfer.items[0].getAsFile()
        }
      } else {
        // Use a interface DataTransfer para acessar o (s) arquivo (s)
        if ($event.dataTransfer?.files?.[0]?.kind === 'file') {
          file = $event.dataTransfer.files[0]
        }
      }
      this.addFile(file)
    },
    addFile(file) {
      const size = file.size && file.size / Math.pow(1000, 2)

      // check file max size
      if (this.maxSize && size > this.maxSize) {
        this.$emit('size-exceeded', { size, maxSize: this.maxSize })
        return
      }

      // update file
      this.file = file
      this.$emit('file', file)

      const reader = new FileReader()

      reader.onload = ($event) => {
        const dataURI = $event.target.result

        if (dataURI) {
          this.$emit('load', {
            data: dataURI,
            localName: this.file.name,
            extension: this.file.name.split('.').pop().toLowerCase()
          })

          this.preview = dataURI
        }
      }

      // read blob url from file data
      reader.readAsDataURL(this.file)
    },
    size(size) {
      const options = ['KB', 'MB', 'GB']
      let i = 0
      let output = size
      for (i = 0; i < options.length; i++) {
        output = output / 1024
        if (output < 1024) {
          break
        }
      }

      return `${output.toFixed(2)} ${options[i === options.length ? i - 1 : i]}`
    }
  }
}
</script>
<style scoped lang="stylus">
.c-drop-file
  &__label
    display flex
    justify-content space-between
    margin-bottom 4px
    width 100%
    text-align left
    font-weight 600
    font-size 14px
  &__container
    border 2px dashed var(--darken-default-grey)
    padding 8px
    border-radius 8px
    width 100%
    height 100%
    &--is-active
      border 2px dashed #AAA
#dropzone
  width  100%
  padding 12px
  border-radius 8px
  background-color var(--default-grey)
  display flex
  align-items center
  justify-content center
</style>
