import readXlsxFile from 'read-excel-file'
export class CsvReader {
  rows = []
  titles = []
  ctx

  constructor(app) {
    this.ctx = app
  }

  read(file) {
    const extension = file.name.split('.').pop().toLowerCase()
    if (extension === 'tsv') {
      return this.readTsv(file)
    }
    if (extension === 'xlsx') {
      return this.readXlsx(file)
    }
  }

  readTsv(file) {
    return new Promise((resolve, reject) => {
      const reader = new FileReader()

      if (file && file.size) {
        reader.readAsText(file)

        reader.onload = () => {
          const raw = typeof reader.result === 'string' ? reader.result : ''
          resolve(raw.split(/\r?\n|\r/).map((row) => row.split(/\t/)))
        }
      } else {
        reject()
      }
    })
  }

  readXlsx(file) {
    return readXlsxFile(file).then((rows) => {
      return rows.map((row) => row.map((i) => `${i}`))
    })
  }
}

export default {
  install: (app) => {
    const csvReader = new CsvReader(app)
    app.config.globalProperties.$csvReader = csvReader
  }
}
