export default {
  methods: {
    githubLinkFromRoute() {
      const { $route } = this
      return `https://github.com/${$route.params.org}/${$route.params.repo}`
    },
    pilots() {
      const { $github, $route, $store, surveys } = this

      return {
        get() {
          return $github
            .getFile(
              `${$route.params.org}/${$route.params.repo}`,
              `database/${$route.params.file}/pilot.json`
            )
            .then(({ data }) => {
              const pilot = JSON.parse(
                Buffer.from(data.content, 'base64').toString('utf8')
              )
              $store.commit('pilots/setPilot', pilot)

              return Promise.resolve({ pilot, sha: data.sha })
            })
        },
        update(updates, message) {
          return this.get().then(({ pilot, sha }) => {
            pilot = {
              ...this.pilot,
              ...pilot,
              ...updates
            }
            return surveys().commit(pilot, message, sha, 'pilot')
          })
        }
      }
    },
    codes(user) {
      const { $github, $route, $store, githubLinkFromRoute, surveys } = this

      return {
        create(dataset = {}) {
          return $github.commit(
            {
              message: `Create Private ${user.id}`,
              content: JSON.stringify(dataset)
            },
            githubLinkFromRoute(),
            `database/${$route.params.file}/coding/${user.id}.json`
          )
        },
        update(updates, message) {
          return this.get().then(({ codes, sha }) => {
            codes = {
              ...(this.codes || {}),
              ...codes,
              ...updates
            }
            console.log(codes)
            return surveys().commit(codes, message, sha, `coding/${user.id}`)
          })
        },
        get() {
          return $github
            .getFile(
              `${$route.params.org}/${$route.params.repo}`,
              `database/${$route.params.file}/coding/${user.id}.json`
            )
            .then(({ data }) => {
              const codes = JSON.parse(
                Buffer.from(data.content, 'base64').toString('utf8')
              )

              $store.commit('codes/setCodes', codes)

              return Promise.resolve({ codes, sha: data.sha })
            })
        }
      }
    },
    repos() {
      const { $github, $route, $store } = this

      return {
        get() {
          return $github
            .getRepo(`${$route.params.org}/${$route.params.repo}`)
            .then(({ data }) => {
              const repo = data

              $store.commit('repos/setRepo', repo)

              return Promise.resolve({ repo, sha: data.sha })
            })
        }
      }
    },
    surveys() {
      const { $github, $route, $store } = this

      return {
        get() {
          return $github
            .getFile(
              `${$route.params.org}/${$route.params.repo}`,
              `database/${$route.params.file}/survey.json`
            )
            .then(({ data }) => {
              const survey = JSON.parse(
                Buffer.from(data.content, 'base64').toString('utf8')
              )
              $store.commit('surveys/setSurvey', survey)

              return Promise.resolve({ survey, sha: data.sha })
            })
        },
        test() {
          console.log(this)
        },
        update(updates, message) {
          return this.get().then(({ survey, sha }) => {
            survey = {
              ...this.survey,
              ...survey,
              ...updates
            }
            return this.commit(survey, message, sha)
          })
        },
        fastUpdate(survey, message, sha) {
          return this.commit(survey, message, sha)
        },
        commit(survey, message, sha, model = 'survey') {
          survey.updated_at = new Date()

          return $github
            .commit(
              {
                message: message || `Update ${survey.name}`,
                content: JSON.stringify(survey),
                sha
              },
              `${$route.params.org}/${$route.params.repo}`,
              `database/${$route.params.file}/${model}.json`
            )
            .then(() => {
              let s = this[model]
              s = {
                ...survey
              }
              if (model === 'pilot') {
                $store.commit('pilots/setPilot', s)
              } else {
                $store.commit('surveys/setSurvey', s)
              }
              return Promise.resolve(survey)
            })
        }
      }
    },
    sentiments() {
      const { surveys } = this
      return {
        create(sentiment) {
          return surveys()
            .get()
            .then(({ survey, sha }) => {
              survey = {
                ...survey,
                sentiments: {
                  ...(survey.sentiments || {}),
                  [sentiment.question]: {
                    ...(survey.sentiments?.[sentiment.question] || {}),
                    [sentiment.answer]: sentiment.value
                  }
                }
              }

              return surveys().fastUpdate(survey, 'Add sentiment', sha)
            })
        }
      }
    },
    comments() {
      const { surveys, codes } = this
      return {
        get() {},
        onAnswer(comment, question, answer, model = 'survey', user = null) {
          comment = {
            ...comment,
            created_at: new Date(),
            private: model === 'codes'
          }
          const entity = model === 'codes' ? codes(user) : surveys()

          return entity.get().then((data) => {
            let survey = data[model]

            survey.comments = {
              ...(survey.comments || {}),
              answers: {
                ...(survey?.comments?.answers || {}),
                [question]: {
                  ...(survey?.comments?.answers?.[question] || {}),
                  [answer]: [
                    ...new Set([
                      ...(survey?.comments?.answers?.[question]?.[answer] ||
                        []),
                      comment
                    ])
                  ]
                }
              }
            }

            return entity.update(
              { comments: survey.comments },
              `Add comment on question ${question}`
            )
          })
        },
        onQuestion(comment, question, model = 'survey', user = null) {
          comment = {
            ...comment,
            created_at: new Date(),
            private: model === 'codes'
          }
          const entity = model === 'codes' ? codes(user) : surveys()
          return entity.get().then((data) => {
            let survey = data[model]

            survey.comments = {
              ...(survey.comments || {}),
              questions: {
                ...(survey?.comments?.questions || {}),
                [question]: [
                  ...new Set([
                    ...(survey?.comments?.questions?.[question] || []),
                    comment
                  ])
                ]
              }
            }
            return entity.update(
              { comments: survey.comments },
              `Add comment on question ${question}`
            )
          })
        },
        deleteFromQuestion(comment, question, model = 'survey', user = null) {
          const entity = model === 'codes' ? codes(user) : surveys()

          return entity.get().then((data) => {
            let survey = data[model]

            let questionComments = survey?.comments?.questions?.[question] || []
            questionComments = questionComments.filter((i) => {
              const check =
                new Date(i.created_at).getTime() ===
                  new Date(comment.created_at).getTime() &&
                i.author === comment.author
              return !check
            })
            const comments = {
              ...survey.comments,
              questions: {
                ...survey.comments.questions,
                [question]: questionComments
              }
            }
            return entity.update({ comments }, `Remove comment`)
          })
        },
        deleteFromAnswer(
          comment,
          question,
          answer,
          model = 'survey',
          user = null
        ) {
          const entity = model === 'codes' ? codes(user) : surveys()

          return entity.get().then((data) => {
            let survey = data[model]

            let answerComments =
              survey?.comments?.answers?.[question]?.[answer] || []
            answerComments = answerComments.filter((i) => {
              const check =
                new Date(i.created_at).getTime() ===
                  new Date(comment.created_at).getTime() &&
                i.author === comment.author
              return !check
            })

            const comments = {
              ...survey.comments,
              answers: {
                ...survey.comments.answers,
                [question]: {
                  ...(survey?.comments?.answers?.[question] || {}),
                  [answer]: answerComments
                }
              }
            }
            console.log(comments)
            return entity.update({ comments }, `Remove comment`)
          })
        }
      }
    },
    tags() {
      const { surveys } = this
      return {
        get() {},
        onQuestion(comment, question) {
          comment = {
            ...comment,
            created_at: new Date()
          }

          return surveys()
            .get()
            .then(({ survey }) => {
              survey.comments = {
                ...(survey.comments || {}),
                questions: {
                  ...(survey?.comments?.questions || {}),
                  [question]: [
                    ...new Set([
                      ...(survey?.comments?.questions?.[question] || []),
                      comment
                    ])
                  ]
                }
              }
              return surveys().update(
                { comments: survey.comments },
                `Add comment on question ${question}`
              )
            })
        }
      }
    }
  }
}
